body {
  padding-bottom: 20px; }

/* Set padding to keep content from hitting the edges */
.body-content {
  padding-left: 15px;
  padding-right: 15px; }

/* Override the default bootstrap behavior where horizontal description lists 
   will truncate terms that are too long to fit in the left column 
*/
.dl-horizontal dt {
  white-space: normal; }

/* Set width on the form input elements since they're 100% wide by default */
input, select, textarea {
  max-width: 280px; }

.navbar {
  background: #eee;
  border-bottom: 10px solid #111458; }

label, fieldset {
  margin-bottom: 2em; }

legend {
  font-size: 1.1em;
  font-weight: bold; }

label, label input, label textarea, label select {
  display: block; }

input[type=date] {
  line-height: 1em; }

input, textarea, select {
  margin-top: .5em;
  padding: .5em; }

textarea {
  width: 30em;
  height: 6em; }

input[type="checkbox"] {
  display: inline;
  margin-right: .6em; }

fieldset label {
  font-weight: normal; }

h1, h2, h3 {
  margin-bottom: 1em; }

.admin-content, .logged-in-content, .logged-out-content {
  display: none !important; }

.admin .top-nav-item.admin-content {
  display: block !important; }

.logged-in .right-nav-item.logged-in-content {
  display: block !important; }

.logged-out .right-nav-item.logged-out-content {
  display: block !important; }

/*Login Form*/
[data-login-modal] .modal-dialog {
  width: 350px; }

/*BugReport*/
[data-bug="people"] label {
  display: inline-block;
  margin-right: 2em;
  width: 280px;
  margin-bottom: 1em; }

.sync {
  float: right; }

.sync-button {
  margin-bottom: 1em;
  float: right;
  clear: right; }
  .sync-button > .glyphicon {
    margin-right: .5em; }

.admin-list-item {
  margin-bottom: .5em; }
  .admin-list-item .btn-default {
    font-size: 1.6em;
    margin-bottom: .5em; }
  .admin-list-item .glyphicon {
    margin-right: 0.3em;
    font-size: .8em; }

.result-section {
  margin-bottom: 2em; }

.result-item {
  border: 1px solid #777777;
  padding: 1em;
  border-radius: 10px;
  margin-right: 3em; }

.result-item-title {
  margin-bottom: .5em;
  margin-top: 0;
  border-bottom: 1px solid #eeeeee;
  text-transform: capitalize;
  padding-bottom: .4em; }

.result-item-value {
  font-size: 5em; }
